import {Injectable} from '@angular/core';
import {Observable} from 'rxjs';
import {Country, City, Office, Employee} from '@pages/staff-bookings/entities';
import {ApiService} from '../../api.service';
import {StaffBTableItem} from '@pages/staff-bookings/staff-b-table/staff-b-table-item';
import {map} from 'rxjs/internal/operators';

@Injectable()
export class StaffBookingsService extends ApiService {
	getCountries(): Observable<Country[]> {
		return this.get<Country[]>('/countries');
	}

	getCities(id: number): Observable<City[]> {
		return this.get<City[]>(`/countries/cities/${id}`);
	}

	getOffices(id: number): Observable<Office[]> {
		return this.get<Office[]>(`/offices/byCity/${id}`);
	}

	getEmployees(): Observable<Employee[]> {
		return this.get<Employee[]>('/users/current/subordinate');
	}

	getStaffBookingsTable(filters: string[]): Observable<StaffBTableItem[]> {
		return this.post<StaffBTableItem[]>('/users/current/reservations/staff', filters).pipe(
			map((data: any) => {
					return data.map((item: any) => {
						return {
								user: item.user,
								country: item.country,
								city: item.city,
								street: item.office.street,
								floor: item.floor,
								place: item.place,
								date: item.detail.date,
								startTime: item.reservation.startTime,
								endTime: item.reservation.endTime
							};
					});
			})
		);
	}
}
