import {
	AfterViewInit,
	Component,
	EventEmitter,
	Input,
	OnChanges,
	OnInit,
	Output,
	SimpleChanges,
	ViewChild
} from '@angular/core';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {MatTable} from '@angular/material/table';
import {StaffBTableDataSource} from './staff-b-table-datasource';
import {StaffBTableItem} from '@pages/staff-bookings/staff-b-table/staff-b-table-item';
import {StaffBookingsService} from '@pages/staff-bookings/staff-bookings.service';

@Component({
	selector: 'app-staff-b-table',
	templateUrl: './staff-b-table.component.html',
	styleUrls: ['./staff-b-table.component.scss']
})
export class StaffBTableComponent implements AfterViewInit, OnInit, OnChanges {
	@ViewChild(MatPaginator) paginator: MatPaginator;
	@ViewChild(MatSort) sort: MatSort;
	@ViewChild(MatTable) table: MatTable<StaffBTableItem>;
	dataSource;
	@Input() filters;
	@Output() getData = new EventEmitter();
	@Input() employees;
	@Input() staffTableData;

	constructor(private staffBookingsService: StaffBookingsService) {}

	displayedColumns = ['employee', 'address', 'floor', 'place', 'date', 'delete'];

	ngOnInit() {
		this.dataSource = new StaffBTableDataSource(this.staffBookingsService);
	}

	ngOnChanges(changes: SimpleChanges) {
		if (changes.staffTableData && this.staffTableData) {
			this.dataSource.data = this.staffTableData;
		}
		if (changes.filters && this.filters) {
			this.getData.emit(this.filters);
		}
		// if (this.filters.length === 0) {
		// 	for (let i = 0; i < 5; i++) {
		// 		this.filters.push('');
		// 	}
		// }
		// this.staffBookingsService.getStaffBookingsTable(this.filters).subscribe((tableItems) => {
		// 	this.dataSource.data = tableItems;
		// });
	}

	ngAfterViewInit() {
		this.dataSource.sort = this.sort;
		this.dataSource.paginator = this.paginator;
		this.table.dataSource = this.dataSource;
	}

	onDelete(): void {}
}
